@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
html {
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-title {
    margin-left: 15vw;
    margin-top: 15vh;
}

.hero {
    font-size: 4em;
    max-width: 80vw;
    font-weight: 700;
    font-family: 'Alata';
}

.first-name {
    color: #4A90E2;
}

.subtitle {
    font-size: 1.75rem;
    font-weight: 400;
    font-family: Inconsolata;
}

@media screen and (min-width: 761px) and (max-width: 1050px) {

    .hero {
        font-size: 3rem;
    }

    .subtitle {
        font-size: 1.5rem !important;
    }
}

@media screen and (max-width: 760px) {

    .hero {
        font-size: 2.2rem;
    }

    .subtitle {
        font-size: 1.1rem !important;
    }
}
.skills-grid {
    margin: 3vh 1vw;
    width: 80vw;
}

.skill-icon {
    margin-right: 3vw;
    margin-bottom: 3vh;
}

@media screen and (max-width: 760px) {
    .skills-grid {
        width: 80vw;
    }
}
.workplace-content {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 80vw;
}

.workplace-logo {
    margin-right: 1vw;
}

.workplace-name {
    font-size: 1.3rem;
    font-family: 'Roboto Slab';
    font-weight: 700;
    margin-bottom: 0;
    margin-top: 1rem;
    text-decoration: none;
}

.workplace-role {
    margin-top: 0;
    margin-bottom: 0;
    font-family: Inconsolata;
    font-size: 1rem;
}

.workplace-desc {
    font-size: 1em;
    color: #6a6a6a;
    max-width: 50vw;
}

button {
    margin-bottom: 5vh;
}

@media screen and (max-width: 760px) {

    .workplace-container {
        margin-bottom: 2em;
    }

    .workplace-name {
        font-size: 1rem;
    }

    .workplace-role {
        font-size: 0.7em;
    }

    .workplace-desc {
        display: none;
    }
}

@media screen and (max-width: 400px) {

    .workplace-name {
        font-size: 1rem;
    }

    .workplace-role {
        font-size: 0.8em;
    }

    .workplace-desc {
        display: none;
    }

}
.project-container {
    margin: 3vh 0;
}

.project-content {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 80vw;
}

.project-logo {
    margin-right: 1vw;
}

.project-name {
    font-size: 1.3rem;
    font-family: 'Roboto Slab';
    font-weight: 700;
    margin-bottom: 0;
    margin-top: 1rem;
    text-decoration: none;
}

.project-type {
    margin-top: 0;
    margin-bottom: 0;
    font-family: Inconsolata;
    font-size: 1rem;
}

.project-desc {
    font-size: 1em;
    color: #6a6a6a;
    max-width: 50vw;
}

button {
    margin-bottom: 5vh;
}

@media screen and (max-width: 760px) {

    .project-container {
        margin-bottom: 2em;
    }

    .project-name {
        font-size: 1rem;
    }

    .project-type {
        font-size: 0.7em;
    }

    .project-desc {
        display: none;
    }

    .project-links, .project-link {
        font-size: 0.7em;
    }
}

@media screen and (max-width: 760px) {

    .project-container {
        margin-bottom: 2em;
    }

    .project-name {
        font-size: 0.8rem;
    }

    .project-type {
        font-size: 0.8em;
    }

    .project-links, .project-link {
        font-size: 0.5em;
    }

    .project-logo {
        width: 48px;
        height: auto;
    }
}
.home-content {
    margin: 15vw;
    overflow-x: hidden;
}

.section {
    margin-top: 15vh;
    margin-left: 15vw;
}

.image-col {
    margin-top: 15vh;
    align-items: center;
}

.profile-pic {
    width: 250px;
    border-radius: 50%;
    border: 2px solid #fafafa;
    margin-top: 3rem;
    margin-left: 3rem;
}

.section-title {
    margin-bottom: 1.5vh;
    font-size: 1.8rem;
    font-weight: 800;
    font-family: Alata;
}

p {
    font-family: 'Roboto Slab';
    font-size: 1.2em;
    text-align: justify;
}

@media screen and (max-width: 760px) {

    .section {
        width: 75vw;
    }

    .image-col {
        margin-top: 5vw;
    }

    .profile-pic {
        width: 125px;
        margin-top: 1.5rem;
        margin-left: 1.5rem;
        display: none;
    }
}

@media screen and (max-width: 400px) {

    .home-title {
        margin-left: 10vw;
    }
    
    .home-content {
        margin-left: 10vw;
    }
    
    .section {
        margin-top: 10vh;
        margin-left: 10vw;
    }
    
}
footer {
    background-color: #f5f5f5;
    font-size: 0.8em;
    width: 100vw;
    padding: 1em;
}

.links {
    margin-left: 10vw;
    margin-top: 5vh;
    margin-bottom: 5vh;
}

.links img {
    margin-right: 1vw;
    width: 32px;
    height: auto;
}

.links img:hover {
    transform: scale(1.2);
}

.signature {
    margin-top: 20px;
}

@media screen and (max-width: 560px) {
    .links img {
        width: 24px;
    }
}
.App-containe {
    width: 100vw;
    overflow-x: hidden;
    overflow: auto;
}
